/* Hide the browser's default radio button */
.swap-cap input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}
.checkmark:hover {
  cursor: pointer;
}
label {
  font-size: 14px;
}
.swap-cap label {
  margin-left: 13px;
}
/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #1a223f;
  border-radius: 50%;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
input:checked ~ .checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.checkmark:after {
  top: 6px;
  left: 6px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #7ed321;
}
.swap-cap {
  width: 100%;
  margin: 0;
  margin: 1rem 0;
}
.new-card {
  margin-top: 10px !important;
  display: none;
}
.new-card input {
  opacity: 1;
}

