.progress-radial {
  display: inline-block;
  position: relative;
  width: 133px;
  height: 133px;
  border-radius: 50%;
  background-color: #599FFF;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
}
.progress-radial::after,
.progress-radial::before {
  content: "";
  width: 18px;
  height: 18px;
  top: 41%;
  left: 41%;
  margin-left: 3.5px;
  margin-top: 3.5px;
  position: absolute;
  z-index: 999;
  border: 3px solid #599FFF;
  box-shadow: rgba(0, 0, 0, 0.2) 10px 0px 10px;
  border-radius: 50%;
  background: rgb(33, 44, 80);
}

.progress-radial:after {
  z-index: 998;
  box-shadow: none;
  transform: translate(0, -58.5px);
}
.progress-radial b:after {
  color: #599FFF;
  position: absolute;
  font-weight: 900;
  left: 50%;
  top: 50%;
  width: 50%;
  height: 50%;
  background-color: #212c50;
  border-radius: 50%;
  margin-left: -25%;
  margin-top: -25%;
  text-align: center;
  line-height: 90px;
  font-size: 30px;
  box-shadow: 0 2px 3px #212c50 inset, 0 0 0 16px #212c50;
}
.progress-0 {
  background-image: linear-gradient(90deg, #293661 50%, transparent 50%, transparent),
    linear-gradient(90deg, #599FFF 50%, #293661 50%, #293661);
}
.progress-0:before {
  transform: rotate(0deg) translate(0, -57.5px);
}
.progress-0 b:after {
