.progress-white-radial {
  display: inline-block;
  position: relative;
  width: 133px;
  height: 133px;
  border-radius: 50%;
  background-color: #C3DEFF;
  box-shadow: 0 2px 15px rgba(0, 0, 0, 0.3);
}
.progress-white-radial::after,
.progress-white-radial::before {
  content: "";
  width: 18px;
  height: 18px;
  top: 41%;
  left: 41%;
  margin-left: 3.5px;
  margin-top: 3.5px;
  position: absolute;
  z-index: 999;
  border: 3px solid #C3DEFF;
  box-shadow: rgba(0, 0, 0, 0.2) 10px 0px 10px;
  border-radius: 50%;
  background: rgb(33, 44, 80);
}

.progress-white-radial:after {
  z-index: 998;
  box-shadow: none;
  transform: translate(0, -58.5px);
}
.progress-white-radial b:after {
  color: #C3DEFF;
  position: absolute;
  font-weight: 900;
  left: 50%;
  top: 50%;
